<template>
	<div class="header">
		<router-link to="/City"  class="left-part">
		<span>{{this.curSelect}}<i class="icon-home"></i></span>
		</router-link>
		<input type="text" name="" id="search" placeholder="请输入搜索内容">
		<span class="right-part icon-music " ></span>
	</div>
</template>
<script type="text/javascript">
import {mapState} from "vuex";
	export default{
		props:{
			city:String,
			letter:String
		},
		computed:{
			...mapState(['curSelect'])
		}
	}
</script>
<style type="text/css" scoped>
.header{
     display: flex;
     height: 0.5rem;
     overflow: hidden;
}
 .left-part{
     min-width: 0.4rem;
     flex-shrink: 0;
}
 .right-part{
     width: 0.4rem;
}
 #search{
     flex: 1;
     min-width: 0.6rem;
     text-align: center;
}

 [class*="icon-"]{
     color: pink;
     font-size: 0.3rem;
     width: 0.4rem;
     height: 0.4rem;
     line-height: 0.4rem;
     text-align: center;
     display: inline-block;
}
 
</style>


