<template>
	<div>
	<back-header theme="登录"></back-header>
	<div class="login_con">
		<img src="../assets/img/logo.png" alt="logo" class="logo">
		<form  id="loginForm" type="post">
			<div class="login_info">
				<input class="input_area" name="keyword" id="username" type="text" placeholder="请输入用户名" ref="username" autocomplete >
				<input class="input_area" name="password" id="pw" type="password" placeholder="请输入密码" ref="password"> 
			</div>
		</form>			
		<div class="other_login">
			<a href="#"><img src="../assets/img/qq.png">qq登录</a>
			<a href="#"><img src="../assets/img/weChat.png">微信登录</a>
		</div>
		<p class="tips">测试账号 用户名:123456 密码:123456</p>
		<!-- <a href="#" class="to_register">没有账号立即注册</a> -->
		<button  type="button" id="loginbtn" @click="toLogin">登&nbsp;&nbsp;&nbsp;&nbsp;录</button>
	</div>
	</div>
</template>
<script>
import backHeader from "common/backHeader.vue"
import {mapState} from "vuex"
import tipShow from "@/assets/js/tips.js"
export default {
	methods:{
		toLogin (){
			var us=this.$refs['username'].value;
			var pw=this.$refs['password'].value;
			if(us.trim()==""||pw.trim()==""){
				tipShow("用户名及密码都不能为空")
			}
			else if((us=="123456"&&pw=="123456")){
				this.$store.commit("signIn",'KZG2605934892');
				tipShow("登录成功")
				var wantgo=this.$route.query.redirect;
				this.$router.replace(wantgo);
			}else{
				tipShow("用户名或密码填写错误")
			}
		}
	},
	components:{
		backHeader
	}
}
	
</script>
<style scoped>
@import url("../assets/css/lr_common.css");
 body{
     background: #fff;
}
 #loginForm{
}
.tips{
	text-align: center;
	padding: 25px 0px 10px;
}

 .to_register{
     float: left;
     -webkit-tap-highlight-color: transparent;
     width: 100%;
     font-size: 0.14rem;
     color: #888;
     text-align: center;
}
 .other_login{
     display:flex;
     justify-content:space-between;
     margin-top:0.1rem;
     padding: 0 0.3rem;
}
 .other_login a{
     color:#333;
     font-size: 0.14rem;
     line-height: 0.32rem;
}
 .other_login img{
     width: 0.26rem;
     height: 0.26rem;
     vertical-align: middle;
     margin-right:0.05rem;
}
 
</style>


